/*移动端*/

//登陆页
import login from '@/components/mob/login';

//首页
import home from '@/components/mob/home';

//路线图
import learn_start from '@/components/mob/learn_start';
import learn_path from '@/components/mob/learn_path';
import learn_detail from '@/components/mob/learn_detail';

//题型
import question from '@/components/mob/question';

//图书馆
import library from '@/components/mob/library';

//竞技场
import ranking_list from '@/components/mob/arena/ranking_list';

//咖啡厅
import coffee from '@/components/mob/coffee';
import teacher from '@/components/mob/coffee/teacher';
import same_class from '@/components/mob/coffee/same_class';
import student from '@/components/mob/coffee/student';
import team from '@/components/mob/coffee/team';

//文章
import blog from '@/components/mob/blog';

//我的（个人中心）
import user_home from '@/components/mob/user/user_home';
import user_home_center from '@/components/mob/user/user_home_center';
import user_home_resume from '@/components/mob/user/user_home_resume';

export default [

    //home
    {
        path: '/mob/',
        component: home
    },

    //login
    {
        path: '/mob/login',
        component: login
    },

    //路线图
    {
        path: '/mob/learn_start',
        component: learn_start,
        meta: {
            check_login: true
        }
    },
    {
        path: '/mob/learn_path',
        component: learn_path,
        meta: {
            check_login: true
        }
    },
    {
        path: '/mob/learn_detail',
        component: learn_detail,
        meta: {
            check_login: true
        }
    },

    //题型
    {
        path: '/mob/question',
        component: question,
        meta: {
            check_login: true
        }
    },

    //图书馆
    {
        path: '/mob/library',
        component: library
    },

    //咖啡厅
    {
        path: '/mob/coffee',
        component: coffee,
        meta: {
            check_login: true
        },
        children: [
            {
                path: 'teacher',
                component: teacher,
                meta: {
                    check_login: true
                }
            },
            {
                path: 'same_class',
                component: same_class,
                meta: {
                    check_login: true
                }
            },
            {
                path: 'student',
                component: student,
                meta: {
                    check_login: true
                }
            },
            {
                path: 'team',
                component: team,
                meta: {
                    check_login: true
                }
            }
        ]
    },

    //文章
    {
        path: '/mob/blog',
        component: blog,
        meta: {
            check_login: true
        }
    },

    //个人中心
    {
        path: '/mob/user_home',
        component: user_home,
        meta: {
            check_login: true
        }
    },
    {
        path: '/mob/user_home_center',
        component: user_home_center,
        meta: {
            check_login: true
        }
    },
    {
        path: '/mob/user_home_resume',
        component: user_home_resume,
        meta: {
            check_login: true
        }
    },

    //竞技场
    {
        path: '/mob/arena_ranking_list',
        component: ranking_list,
        meta: {
            check_login: true
        }
    },
]



