<template>
    <div class="banner-widget">
        <div class="container">
            <img class="b-w-bg" :src="cur_res.image" />
            <p class="b-w-title">{{cur_res.name}}</p>
            <div class="b-w-desc basic-article-widget">
                <p class="ba-w-p" v-for="desc of cur_res.content">{{desc}}<p/>
            </div>
            <p>
                <router-link :to="path_prefix + '/learn_start?class_id=' + major_id" class="b-w-btn">立即启程</router-link>
            </p>
        </div>
    </div>
</template>

<script>
    export default {
        name: "banner-widget",
        data(){
            return {

                /*基于终端的路径前缀*/
                path_prefix: "",

                /*当前使用资源名*/
                res_src_name: "",

                /*banner资源*/
                banner_src: "",

                /*移动端banner资源*/
                mob_banner_src: "",

                /*一级课程id*/
                major_id: ""

            }
        },
        computed: {

            /*当前使用资源*/
            cur_res(){
                let bs = this[this.res_src_name] || {};
                if(bs && bs.content){
                    bs.content = bs.content.split("\\n");
                }
                return bs;
            },

            /*一级课程*/
            major(){
                return this.$store.state.major;
            }

        },
        watch: {
            major(id){
                //this.classes.push(this.$store.state.major);//初期只取一项一级课程
                this.major_id = id;
            }
        },
        mounted(){
            let res_src_name;
            if(this.$utils.cheack_device(this.$route) === "mob"){
                res_src_name = "mob_banner_src";
                this.path_prefix = "/mob";
            }else{
                res_src_name = "banner_src";
                this.path_prefix = "";
            }
            this.res_src_name = res_src_name;
            this.$utils.get_res(res_src_name, this);
            this.major_id = this.major;
        }
    }
</script>

<style scoped>
    @import "../../assets/css/basic-article-widget.css";
    @import "../../assets/css/banner-widget.css";
</style>


