<template>
    <div>
        <matte-video-widget :video="matte_video"></matte-video-widget>
        <tab-widget :tabs="tabs"></tab-widget>
        <div style="padding: 2rem;">

            <!--聊天记录-->
            <div class="chat-record-widget" ref="chatRecord" state="show">
                <ul v-if="chat_record.record.length > 0" >
                    <li v-for="record of chat_record.record.slice(-50)" :class="{'cr-w-item':true, 'cr-w-item-left':record.from === 'you', 'cr-w-item-right':record.from === 'me'}" @click="check_chat">
                        <div>
                            <!--我发起-->
                            <head-widget :uname="user.uname" :color="true" v-if="record.from === 'me'"></head-widget>
                            <!--对方发起-->
                            <head-widget :uname="record.name" v-else></head-widget>
                        </div>
                        <div class="cr-w-cont-box">
                            <div class="cr-w-time">{{time_format(record.date_time)}}</div>
                            <div class="cr-w-text">
                                <div v-html="record.desc"></div>
                                <video v-if="record.type == 'video'" controls class="cr-w-video">
                                    <source src="/static/res/video/video_2.mp4"/>
                                </video>
                            </div>
                        </div>
                    </li>
                </ul>
                <div v-else class="no-result-widget">
                    <span class="icon-nodata nr-w-ico"></span>
                    <span class="nr-w-text">暂无聊天记录</span>
                </div>
            </div>

            <!--编辑器-->
            <div class="tinymce-widget" ref="tinymce" state="show">
                <tiny-editor v-model="tinymce.html" :init="tinymce.setting"></tiny-editor>
                <div class="t-w-editor">
                    <input hidden style="display: none" type="file" accept="image/*" ref="img_uploader" @change="upload_img">
                    <a class="base-btn-widget t-w-back-btn" state="visied" @click="back">退出小组</a>
                    <a class="base-btn-widget" @click="send_msg">发送消息</a>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    import Tab from "@/components/widget/tab-widget";
    import Head from "@/components/widget/head-widget";
    import MatteVideo from "@/components/widget/matte-video-widget";
    import 'tinymce/tinymce'
    import 'tinymce/langs/zh_CN.js'
    import 'tinymce/themes/modern/theme'
    import Editor from '@tinymce/tinymce-vue'
    import 'tinymce/plugins/image'
    import 'tinymce/plugins/emoticons'
    import 'tinymce/plugins/emoticons'

    export default {
        name: "teacher",
        data() {
            return {

                /*tab-widget*/
                tabs: [
                    {
                        name: "老师",
                        type: "route",
                        link: "/mob/coffee/teacher",
                    },
                    {
                        name: "同班",
                        type: "route",
                        link: "/mob/coffee/same_class",
                    },
                    {
                        name: "小组",
                        state: "act",
                        type: "route",
                        link: "/mob/coffee/team",
                    },
                    {
                        name: "同学",
                        type: "route",
                        link: "/mob/coffee/student",
                    },
                ],

                /*当前小组*/
                cur_team: {},

                /*当前小组的全部聊天*/
                mail_list: [],

                /*当前用户*/
                user: this.$store.state.user,

                /*聊天记录*/
                chat_record: {
                    talk_to: {
                        uid: "",
                        name: ""
                    },
                    record: []
                },

                /*富文本编辑器*/
                tinymce: {
                    html: "",
                    setting: {
                        menu: {},
                        language: "zh_CN",
                        content_style: "body {min-height: 70px} img {max-width: 100%; height: auto}",
                        height: 70,
                        min_height: 70,
                        skin_url: '/static/dist/tinymec/skins/lightgray',
                        plugins: 'image emoticons',
                        toolbar: 'image emoticons',
                        branding: false,
                        statusbar: false,
                        images_upload_url: "http://wyrun.com/btx/index/upload_img/"
                    }
                },

                /*弹窗*/
                confirm: null,

                /*遮罩视频*/
                matte_video: {
                    state: "hide",
                    title: "",
                    video: ""
                }

            }
        },
        methods: {

            /*获取当前小组*/
            get_team(){
                this.$axios.get("group_user_info/").then(res => {
                    this.cur_team = res.data[0];
                })
            },

            /*获取当前小组的全部聊天*/
            get_mail_list_by_team(){
                this.$axios.all([
                    this.$axios.get("group_msg/"),
                    this.$axios.get("group_stu_teacher_msg/")
                ]).then(this.$axios.spread((r1, r2) => {
                    this.mail_list = r1.data;
                    r2.data.forEach(res => {//老师发送消息
                        this.mail_list.push({
                            group: res.group,
                            id: res.id,
                            send_time: res.send_time,
                            message: res.message,
                            user: {
                                id: res.teacher.id,
                                name: res.teacher.name
                            },
                        });
                    })
                    this.init_msg();
                }))
            },

            /*发送消息*/
            send_msg() {
                let msg = this.tinymce.html;
                let msg_tmp = msg.replace(/&nbsp;|<p>|<\/p>|\s/g, "");
                if(msg_tmp.length === 0){//输入非空验证
                    this.confirm.toast("无内容输入!", 2000, "notic");
                }else{
                    let date_time = this.$utils.gen_date_time();

                    this.$axios.post("group_msg_create/", {
                        message: msg,
                        send_time: date_time,
                        user: this.user.uid,
                        group: this.cur_team.group.id
                    }).then(res => {
                        this.chat_record.record.push({
                            "from": "me",
                            "desc": msg,
                            "date_time": date_time
                        });
                        this.tinymce.html = "";
                        this.top_msg();
                    })
                }
            },

            /*置顶聊天记录*/
            top_msg(){
                let $chat_record = this.$refs.chatRecord;
                if($chat_record){
                    this.$nextTick(function(){
                        $chat_record.scrollTop = $chat_record.scrollHeight;//置顶聊天记录
                    })
                }
            },

            /*查看消息资源*/
            check_chat(e) {
                let targ = e.target,
                    src;
                if(targ.nodeName.toLowerCase() === "img"){//图片资源
                    src = targ.src;
                    src && window.open(src, "_blank");
                }else if(targ.className === "cr-w-wiki"){//wiki多媒体资源
                    src = targ.getAttribute("id");
                    src && this.open_video_matte(targ.title, src);
                }
            },

            /*初始化聊天记录*/
            init_msg(){
                this.chat_record.record = [];
                this.mail_list.forEach(mail => {
                    this.chat_record.record.push({
                        from: mail.from = mail.user.id === this.user.uid? "me": "you",
                        name: mail.user.name,
                        desc: mail.message,
                        date_time: mail.send_time,
                        unread: 1
                    });
                })

                /*基于时间排序消息*/
                this.chat_record.record.sort((fr,af) => {
                    if(fr.date_time < af.date_time) return -1;
                })

                this.top_msg();
            },

            /*格式化时间*/
            time_format(time) {
                let time_arr = time.substr(2).replace("T", " ").replace(/-/g, "/").split(":");
                time_arr.pop();
                return time_arr.join(":");
            },

            /*回退上级*/
            back(){
                this.$router.go(-1);
            },

            /*开启视频层*/
            open_video_matte(title, video){
                this.matte_video = {
                    state: "show",
                    title: title,
                    video: video
                };
            },

            /*上传图片*/
            upload_img(e){
                let form_data = new FormData();
                form_data.append("file", e.currentTarget.files[0]);
                this.$axios({
                    method: 'post',
                    url: "http://wyrun.com/btx/index/upload_img/",
                    data: form_data,
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }).then(res => {
                    this.tinymce.html = "<p><img src='" + res.data.location + "' /></p>";
                    this.send_msg();
                })
            }

        },
        components: {
            "tab-widget": Tab,
            "head-widget": Head,
            "tiny-editor": Editor,
            "matte-video-widget": MatteVideo
        },
        beforeMount(){

            /*上传图片组件*/
            let _this = this;
            this.tinymce.setting.toolbar = 'img emoticons';
            this.tinymce.setting.setup = function(editor) {
                editor.addButton('img', {
                    icon: "image",
                    onclick: function(){
                        _this.$refs["img_uploader"].click();
                    }
                });
            };

        },
        mounted() {
            this.confirm = this.$B.widgets.Confirm;
            this.get_team();
            this.get_mail_list_by_team();

            /*定时刷新*/
            setInterval(() => {
                this.get_mail_list_by_team();
            }, 60000)

            /*移动端移除上传按钮的touchstart事件*/
            let t = setInterval(()=>{
                var mceu0 = document.querySelector(".mce-first");
                if(mceu0){
                    mceu0.addEventListener("touchstart", function(e){
                        e.stopPropagation();
                    });
                    clearInterval(t);
                }
            }, 1000)

        },
    }
</script>

<style scoped>
    @import "../../../assets/css/base-title-widget.css";
    @import "../../../assets/css/basic-article-widget.css";
    @import "../../../assets/css/base-btn-widget.css";
    @import "../../../assets/css/head-widget.css";
    @import "../../../assets/css/no-result-widget.css";
    @import "../../../assets/css/tinymce-widget.css";
</style>
<style>
    @import "../../../assets/css/chat-record-widget.css";
</style>



