<template>
    <div>
        <h2 class="main-title-widget">{{class_info.name}}</h2>
        <div class="learn-outline-widget">
            <dl class="lo-w-list pannel-widget" v-for="(item, index) of class_info.list">
                <dt class="base-title-widget"><h3>{{item.title}}</h3></dt>
                <dd class="grid lo-w-list-items">
                    <div class="col-6" :id="data.id" :data-name="data.name" :data-desc="data.desc" v-for="data of item.datas">
                        <router-link :rank="data.rank" :to="'/mob/learn_path?cid=' + data.id + '&cname=' + data.name" :select="data.select" class="lo-w-list-item">
                            <img class="lo-w-list-item-cover" :src="show_major_cover(data.cover)" />
                            <div class="lo-w-list-item-name"><i v-if="data.rank>1" class="star-ico-widget icon-wuxing lo-w-list-item-ico" :rank="data.rank"></i>{{data.name}}</div>
                        </router-link>
                    </div>
                </dd>
            </dl>
        </div>
    </div>
</template>

<script>
    import ClassManager from '@/lib/ClassManager';

    var user_learn_progress = {//【data:用户学习进度】
        lasted: "0",
    };

    export default {
        name: "learn_start",
        data() {
            return {

                /*课程大纲*/
                class_info: {
                    id: "",
                    name: "",
                    list: []
                },

                /*弹窗*/
                confirm: null,

            }
        },
        methods: {

            /*显示课程图片*/
            show_major_cover(url){
                return url?url:"data:image/jpg;base64,";
            }

        },
        mounted() {
            this.confirm = this.$B.widgets.Confirm;
            this.confirm.load("课程加载中");
            this.$axios.all([
                this.$axios.get("major/" + this.$route.query.class_id + "/"),
                this.$axios.get("course/"),
                this.$axios.get("complete/"),
            ]).then(this.$axios.spread((r1, r2, r3) => {
                let cur_class = r1.data,
                    list = [],
                    courses = r2.data;

                /*用户学习进度*/
                let progress = r3.data;
                progress.forEach(pro => {
                    let course_id = pro.chapter.course_name.id;
                    if(pro.course_end === true){
                        user_learn_progress[course_id] = 2;//学完即评星（默认铜星）
                    }else{
                        user_learn_progress[course_id] = 1;//学过即显示
                    }
                    user_learn_progress.lasted = course_id;
                });

                /*构造二级课程*/
                cur_class.sub_cat.forEach(cat => {
                    let datas = [];
                    list.push({
                        title: cat.name,
                        datas: datas
                    })

                    /*构造三级课程*/
                    for (var i = 0, course, cur_course; i < courses.length; i++) {
                        course = courses[i];
                        if (cat.id === course.category.id) {
                            cur_course = courses.splice(i, 1)[0]["course"];
                            datas.push({
                                id: cur_course.id,
                                name: cur_course.name,
                                desc: cur_course.course_task,
                                cover: cur_course.image,
                            });
                            i--;
                        }
                    }

                })

                /*执行初始化*/
                let class_info_data = {
                        id: cur_class.id,
                        name: cur_class.name,
                        list: list
                    },
                    class_manager = new ClassManager(user_learn_progress, class_info_data);
                this.class_info = class_manager.get_class_outline();
                this.$nextTick(function(){
                    document.documentElement.scrollTop = document.body.scrollTop = 60;
                    this.confirm.load("课程加载完成", "end");
                })
            }))
        },
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/basic-article-widget.css";
    @import "../../assets/css/base-btn-widget.css";
    @import "../../assets/css/class-desc-widget.css";
    @import "../../assets/css/main-title-widget.css";
    @import "../../assets/css/copyright-widget.css";
    @import "../../assets/css/learn-outline-widget.css";
    @import "../../assets/css/star-ico-widget.css";
</style>



