<template>
    <div>
        <div class="pannel-widget" style="padding-bottom: 3rem">
            <tab-widget :tabs="tabs"></tab-widget>

            <!--排行榜组件-->
            <div class="base-table-widget" v-if="cur_rank.length > 0">
                <table class="bt-w-table">
                    <thead>
                    <tr class="bt-w-title">
                        <td class="bt-w-title-td">学员</td>
                        <td class="bt-w-title-td pcenter">分数</td>
                        <td class="bt-w-title-td pcenter">排名</td>
                    </tr>
                    </thead>
                    <tbody>
                    <tr class="bt-w-tr" v-for="(user, index) of cur_rank">
                        <td class="bt-w-td">
                            <head-widget :uname="user.name"></head-widget>
                            <span>{{user.name}}</span>
                        </td>
                        <td class="bt-w-td pcenter">{{user.score}}</td>
                        <td class="bt-w-td pcenter"><i class="icon-wuxing star-ico-widget"
                                                       :rank="4-index" v-if="index<3"></i> <span
                                class="bt-w-score">{{index+1}}</span></td>
                    </tr>
                    </tbody>
                </table>
            </div>
            <div class="no-result-widget" style="padding-bottom: 44rem" v-else>
                <span class="icon-nodata nr-w-ico"></span>
                <span class="nr-w-text">暂无排行榜数据</span>
            </div>

        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";
    import Tab from "@/components/widget/tab-widget";
    import Head from "@/components/widget/head-widget";

    export default {
        name: "ranking_list",
        data() {
            var vue = this,
                rank_data = {//【data:排行榜数据】
                    total_rank: [
                        {
                            uid: "u1",
                            name: "张雨生",
                            score: "340",
                        },
                        {
                            uid: "u2",
                            name: "周洁",
                            score: "120",
                        },
                        {
                            uid: "u3",
                            name: "王林",
                            score: "113",
                        },
                        {
                            uid: "u4",
                            name: "刘建明",
                            score: "107",
                        },
                        {
                            uid: "u5",
                            name: "吴建豪",
                            score: "101",
                        },
                        {
                            uid: "u6",
                            name: "周子轩",
                            score: "98",
                        },
                        {
                            uid: "u7",
                            name: "杨洋",
                            score: "85",
                        },
                    ],
                    week_rank: [
                        {
                            uid: "u6",
                            name: "周子轩",
                            score: "98",
                        },
                        {
                            uid: "u7",
                            name: "杨洋",
                            score: "85",
                        },
                    ],
                    day_rank: [
                        {
                            uid: "u4",
                            name: "刘建明",
                            score: "107",
                        },
                        {
                            uid: "u5",
                            name: "吴建豪",
                            score: "101",
                        },
                    ]
                };

            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "排行榜",
                        act: "act",
                        type: "route",
                        link: "/mob/arena_ranking_list",
                    }
                ],

                /*tab_widget*/
                tabs: [
                    {
                        name: "总排行",
                        state: "act",
                        callback() {
                            vue.cur_rank = vue.rank_data["total_rank"];
                        }
                    },
                    {
                        name: "月排行",
                        state: "",
                        callback() {
                            vue.cur_rank = vue.rank_data["week_rank"];
                        }
                    },
                    {
                        name: "班级排行",
                        state: "",
                        callback() {
                            vue.cur_rank = vue.rank_data["day_rank"];
                        }
                    },
                ],

                /*全部排行榜*/
                rank_data: {
                    total_rank: [],
                    week_rank: [],
                    day_rank: [],
                },

                /*当前排行榜*/
                cur_rank: [],

            }
        },
        components: {
            "tab-widget": Tab,
            "aside-nav-widget": AsideNav,
            "head-widget": Head,
        },
        methods: {

            /*排名排序*/
            rank_sort(rank_data) {
                rank_data.sort((r1, r2) => {
                    return r1.score > r2.score ? false : true;
                })
            }

        },
        mounted() {
            this.$axios.get("day_rank/")
                .then(res => {
                    let ranks = res.data;
                    ranks.forEach(data => {
                        let uid = data.user.id,
                            uname = data.user.name;
                        this.rank_data.total_rank.push({
                            uid: uid,
                            name: uname,
                            score: data.total_ranking,
                        });
                        this.rank_data.week_rank.push({
                            uid: uid,
                            name: uname,
                            score: data.monthly_rankings,
                        })
                        this.rank_data.day_rank.push({
                            uid: uid,
                            name: uname,
                            score: data.class_rankings,
                        })
                    })

                    /*排序*/
                    this.rank_sort(this.rank_data.total_rank);
                    this.rank_sort(this.rank_data.week_rank);
                    this.rank_sort(this.rank_data.day_rank);

                    this.cur_rank = this.rank_data.total_rank;//默认显示总排名
                    document.documentElement.scrollTop = document.body.scrollTop = 60;
                })
        }
    }
</script>

<style scoped>
    @import "../../../assets/css/pannel-widget.css";
    @import "../../../assets/css/seg-widget.css";
    @import "../../../assets/css/base-title-widget.css";
    @import "../../../assets/css/aside-nav-widget.css";
    @import "../../../assets/css/base-table-widget.css";
    @import "../../../assets/css/star-ico-widget.css";
    @import "../../../assets/css/no-result-widget.css";
</style>


