<template>
    <div>
        <ul class="tab-widget">
            <li class="t-w-item" v-for="(tab, index) of tabs" :index="index" :state="tab.unread" @click="tab_sel">
                <router-link class="t-w-item-btn" :state="tab.state" :to="tab.link" v-if="tab.type === 'route'">{{tab.name}}</router-link>
                <span class="t-w-item-btn" :state="tab.state" v-else>{{tab.name}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "tab-widget",
        props: ["tabs"],
        methods: {

            /*标签触发*/
            tab_sel(e) {
                let $btn = e.currentTarget,
                    cur_index = $btn.getAttribute("index");
                this.tabs.forEach(function (tab, index) {
                    if (tab.state === "act") tab.state = "";
                    if (index == cur_index) {
                        tab.callback && tab.callback();//执行回调函数
                        tab.state = "act";
                    }
                })
            }

        }
    }
</script>

<style scoped>
    @import "../../assets/css/tab-widget.css";
</style>


