<template>
    <div style="padding-bottom: 3rem">
        <div class="pannel-widget">
            <router-view></router-view>
        </div>

        <div class="pannel-widget">
            <h3 class="base-title-widget bt-w-blue">公告栏</h3>
            <hr class="seg-widget"/>
            <div class="basic-article-widget pad-w-2">
                <p class="ba-w-p">{{news.public_notic}}</p>
            </div>
        </div>
        <div class="pad-h-1"></div>

        <!--消息历史-->
        <div class="pannel-widget">
            <h3 class="base-title-widget bt-w-blue">最新消息</h3>
            <hr class="seg-widget"/>
            <div v-if="history_msg.length > 0">
                <div class="pad-w-2" v-for="(msg,i) of history_msg.slice(0,4)">
                    <div class="basic-article-widget" >
                        <p class="ba-w-p"><a @click="to_chapter" :data-mission="msg.mission_id">【提醒】 {{msg.teacher}} 老师,点评了你所提交的作业</a></p>
                        <p class="ba-w-small-p pright">
                            <span class="text-strong">{{msg.date_time}}</span>
                        </p>
                    </div>
                    <hr class="seg-widget" state="max_line" v-if="history_msg.length-1 > i"/>
                </div>
            </div>
            <div class="no-result-widget" v-else>
                <span class="icon-nodata nr-w-ico"></span>
                <span class="nr-w-text">暂无点评</span>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "coffee",
        data(){
            var news = {//【data:消息资讯】
                public_notic: "Proin sodales pulvinar tempor Cum sociis natoque penatibus et magnis dis parturient montes.",
                lasted_info: [
                    "1.Proin sodales pulvinar tempor Cum sociis natoque penatibus et magnis dis parturient montes.",
                    "2.Proin sodales pulvinar tempor Cum sociis natoque penatibus et magnis dis parturient montes.",
                    "3.Proin sodales pulvinar tempor Cum sociis natoque penatibus et magnis dis parturient montes.",
                ],
            };

            return {

                /*消息资讯*/
                news: {
                    public_notic: "",
                    lasted_info: []
                },

                /*老师点评消息*/
                history_msg: [],

                /*弹窗*/
                confirm: null,

            }
        },
        methods: {

            /*连接至课程章节*/
            to_chapter(e){
                this.$axios.get("chaptertask/")
                    .then( tasks => {
                        tasks.data.forEach( task => {
                            if(task.id == e.target.dataset.mission){
                                this.$router.push("/mob/learn_detail?sub_c_id=" + task.chapter_name + "&task_id=" + task.id);
                                return;
                            }
                        })
                    })
            }

        },
        mounted(){
            this.confirm = this.$B.widgets.Confirm;
            this.confirm.load("聊天数据加载中");
            this.$axios.all([
                this.$axios.get("user_class/"),
                this.$axios.get("evaluation/"),
            ]).then(
                this.$axios.spread((r1, r2)=>{

                    /*公告*/
                    this.news.public_notic = r1.data[0].notice;

                    /*初始化消息列表*/
                    let msgs = r2.data;
                    msgs.forEach(msg => {
                        if (msg.user === this.$store.state.user.uid) {
                            this.history_msg.push({
                                history_msg_id: msg.id,
                                mission_id: msg.mission,
                                teacher: msg.teacher.name,
                                date_time: msg.evaluation_time.replace("T", " ")
                            });
                        }
                    });
                    this.history_msg.reverse();

                    this.$nextTick(function(){
                        document.documentElement.scrollTop = document.body.scrollTop = 60;
                        this.confirm.load("聊天数据加载完成", "end");
                    })
                })
            )
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/basic-article-widget.css";
    @import "../../assets/css/no-result-widget.css";
    @import "../../assets/css/seg-widget.css";
</style>


