<template>
    <div class="grid">
        <matte-video-widget :video="matte_video"></matte-video-widget>

        <!--通讯录-->
        <div class="col-4 col-mob-12" style="padding: 0 2rem">
            <div class="search-widget">
                <div class="s-w-inp">
                    <input type="text" placeholder="搜索" class="s-w-inp-text" maxlength="6" v-model="serch_uname">
                </div>
                <span class="s-w-btn" @click="serch_user"><span class="icon-sousuo s-w-sousuo-ico"></span></span>
            </div>
            <div class="mail-list-widget">
                <div class="ml-w-title">我的通讯录</div>
                <ul class="ml-w-ul" v-if="mail_list.length > 0">
                    <li class="ml-w-li" v-for="user of mail_list" :state="user.state" :id="user.uid" @click="sel_user">
                         <head-widget :uname="user.name" :msg-count="user.unread"></head-widget>
                         <span class="ml-w-name" v-text="user.suffix?user.suffix:user.name"></span>
                    </li>
                </ul>
                <div v-else class="no-result-widget">
                    <span class="icon-nodata nr-w-ico"></span>
                    <span class="nr-w-text">未找到该用户</span>
                </div>
            </div>
        </div>

        <div class="col-8 col-mob-12" style="padding: 0 2rem;">

            <!--聊天记录-->
            <div class="chat-record-widget" ref="chatRecord">
                <ul v-if="chat_record.record.length > 0" >
                    <li v-for="record of chat_record.record.slice(-50)" :class="{'cr-w-item':true, 'cr-w-item-left':record.from === 'you', 'cr-w-item-right':record.from === 'me'}" @click="check_chat">
                        <div>
                            <!--我发起-->
                            <head-widget :uname="user.uname" :color="true" v-if="record.from === 'me'"></head-widget>
                            <!--对方发起-->
                            <head-widget :uname="chat_record.talk_to.name" v-else></head-widget>
                        </div>
                        <div class="cr-w-cont-box">
                            <div class="cr-w-time">{{time_format(record.date_time)}}</div>
                            <div class="cr-w-text">
                                <div v-html="record.desc"></div>
                                <video v-if="record.type == 'video'" controls class="cr-w-video">
                                    <source src="/static/res/video/video_2.mp4"/>
                                </video>
                            </div>
                        </div>
                    </li>
                </ul>
                <div v-else class="no-result-widget">
                    <span class="icon-nodata nr-w-ico"></span>
                    <span class="nr-w-text">暂无聊天记录</span>
                </div>
            </div>

            <!--编辑器-->
            <div class="tinymce-widget" ref="tinymce">
                <tiny-editor v-model="tinymce.html" :init="tinymce.setting"></tiny-editor>
                <div class="t-w-editor">
                    <input hidden style="display: none" type="file" accept="image/*" ref="img_uploader" @change="upload_img">
                    <a class="base-btn-widget t-w-back-btn" state="visied" @click="back_list">返回列表</a>
                    <a class="base-btn-widget" @click="send_msg">发送消息</a>
                </div>
            </div>

        </div>

    </div>
</template>

<script>
    import Head from "@/components/widget/head-widget";
    import MatteVideo from "@/components/widget/matte-video-widget";
    import 'tinymce/tinymce'
    import 'tinymce/langs/zh_CN.js'
    import 'tinymce/themes/modern/theme'
    import Editor from '@tinymce/tinymce-vue'
    import 'tinymce/plugins/image'
    import 'tinymce/plugins/emoticons'
    import 'tinymce/plugins/emoticons'

    export default {
        name: "editor-widget",
        props: ["mail-list", "obj"],
        data() {
            var _this = this;

            return {

                /*当前用户*/
                user: this.$store.state.user,

                /*通讯录*/
                mail_list: this.mailList,

                /*搜索用户名*/
                serch_uname: "",

                /*聊天记录*/
                chat_record: {
                    talk_to: {
                        uid: "",
                        name: ""
                    },
                    record: []
                },

                /*富文本编辑器*/
                tinymce: {
                    html: "",
                    setting: {
                        menu: {},
                        language: "zh_CN",
                        content_style: "body {min-height: 70px} img {max-width: 100%; height: auto}",
                        height: _this.$utils.cheack_device(_this.$route) === "pc"?157:70,
                        min_height: 70,
                        //document_base_url: 'http://www.geziwangshu.com/static/dist/',
                        skin_url: '/static/dist/tinymec/skins/lightgray',
                        plugins: 'image emoticons',
                        toolbar: 'image emoticons',
                        branding: false,
                        statusbar: false,
                        images_upload_url: "http://wyrun.com/btx/index/upload_img/"
                    }
                },

                /*弹窗*/
                confirm: null,

                /*遮罩视频*/
                matte_video: {
                    state: "hide",
                    title: "",
                    video: ""
                }

            }
        },
        methods: {

            /*搜索用户*/
            serch_user() {
                if(!this.serch_uname){//无搜索字段，返回全部
                    this.mail_list = this.mailList;
                }else{
                    this.mail_list = [];
                    this.mailList.forEach(user => {
                        if(user.name.search(this.serch_uname) > -1){//有结果（模糊搜索）
                            this.mail_list.push(user);
                            return;
                        }
                    })
                }
                this.serch_uname = "";
            },

            /*切换对话用户*/
            sel_user(e) {
                this.mailList.forEach(user => {
                    let uid = user.uid;
                    if (user.state === "act") user.state = "";
                    if (uid == e.currentTarget.id) {

                        /*移动端交互*/
                        if(this.$utils.cheack_device(this.$route) === "mob"){
                            this.$refs.chatRecord.setAttribute("state", "show");
                            this.$refs.tinymce.setAttribute("state", "show");
                        }

                        user.state = "act";
                        this.chat_record.talk_to = {//对话方
                            uid: uid,
                            name: user.name
                        }
                        this.chat_record.record = this.chat_record[uid].msg;
                        this.top_msg();
                        this.clear_unread(user);
                    }
                })
            },

            /*消除未读消息*/
            clear_unread(user){
                if(user.unread != -1){
                    this.chat_record.record.forEach(chat => {
                        if(chat.status == 0 && chat.from === "you"){
                            this.$axios.put(this.obj + "_msg_status/" + chat.id + "/", {msg_status: 1}).then(res => {
                                user.unread = -1;
                            })
                        }
                    })
                };
            },

            /*发送消息*/
            send_msg() {
                let msg = this.tinymce.html,
                    talk_to_uid = this.chat_record.talk_to.uid,
                    send_data = {};
                if(talk_to_uid){
                    let msg_tmp = msg.replace(/&nbsp;|<p>|<\/p>|\s/g, "");
                    if(msg_tmp.length === 0){//输入非空验证
                        this.confirm.toast("无内容输入!", 2000, "notic");
                    }else{
                        let date_time = this.$utils.gen_date_time();
                        if(this.obj === "student"){//学生
                            send_data = {
                                message: "",
                                rich_message: msg,
                                send_time: date_time,
                                msg_status: 0,
                                send_student: this.user.uid,
                                rev_student: talk_to_uid
                            }
                        }else if(this.obj === "teacher"){//老师
                            send_data = {
                                message: "1",
                                rich_message: msg,
                                send_time: date_time,
                                msg_type: 0,//0:学生消息，1:老师消息
                                msg_status: 0,
                                student: this.user.uid,
                                teacher: talk_to_uid
                            }
                        }
                        this.$axios.post(this.obj + "_msg/", send_data).then(res => {
                            this.chat_record.record.push({
                                "from": "me",
                                "desc": msg,
                                "date_time": date_time
                            });
                            this.tinymce.html = "";
                            this.top_msg();
                        })
                    }
                }else{
                    this.confirm.toast("请先选择通讯录联系人", 2000, "notic");
                }
            },

            /*置顶聊天记录*/
            top_msg(){
                let $chat_record = this.$refs.chatRecord;
                if($chat_record){
                    this.$nextTick(function(){
                        $chat_record.scrollTop = $chat_record.scrollHeight;//置顶聊天记录
                    })
                }
            },

            /*查看消息资源*/
            check_chat(e) {
                let targ = e.target,
                    src;
                if(targ.nodeName.toLowerCase() === "img"){//图片资源
                    src = targ.src;
                    src && window.open(src, "_blank");
                }else if(targ.className === "cr-w-wiki"){//wiki多媒体资源
                    src = targ.getAttribute("id");
                    src && this.open_video_matte(targ.title, src);
                }
            },

            /*初始化聊天记录*/
            init_msg(){
                this.mailList.forEach(mail => {
                    this.chat_record[mail.uid] = {
                        name: mail.name,
                        msg: [],
                        unread: 0
                    }
                })
                let msg_callback;
                if(this.obj === "student"){//学生
                    msg_callback = (msg) => {
                        if(this.chat_record[msg.send_student]){//对方发送消息
                            if(msg.msg_status == 0) this.chat_record[msg.send_student].unread++;//记录未读消息
                            this.chat_record[msg.send_student].msg.push({
                                "from": "you",
                                "desc": msg.rich_message,
                                "date_time": msg.send_time,
                                "id": msg.id,
                                "status": msg.msg_status
                            });
                        }else if(this.chat_record[msg.rev_student]){//我发送消息
                            this.chat_record[msg.rev_student].msg.push({
                                "from": "me",
                                "desc": msg.rich_message,
                                "date_time": msg.send_time,
                                "id": msg.id,
                                "status": msg.msg_status
                            });
                        }
                    }
                }else if(this.obj === "teacher"){//老师
                    msg_callback = (msg) => {
                        if(msg.msg_type == 1){//对方发送消息
                            if(msg.msg_status == 0) this.chat_record[msg.teacher].unread++;//记录未读消息
                            this.chat_record[msg.teacher].msg.push({
                                "from": "you",
                                "desc": msg.rich_message,
                                "date_time": msg.send_time,
                                "id": msg.id,
                                "status": msg.msg_status
                            });
                        }else if(msg.msg_type == 0){//我发送消息
                            this.chat_record[msg.teacher].msg.push({
                                "from": "me",
                                "desc": msg.rich_message,
                                "date_time": msg.send_time,
                                "id": msg.id,
                                "status": msg.msg_status
                            });
                        }
                    }
                }
                this.$axios.get(this.obj + "_msg/").then(res => {
                    res.data.forEach(msg_callback)

                    /*基于时间排序消息&绑定未读消息*/
                    this.mailList.forEach(mail => {
                        mail.unread = this.chat_record[mail.uid].unread;//绑定未读消息
                        this.chat_record[mail.uid].msg.sort((fr,af) => {
                            if(fr.date_time < af.date_time){
                                return -1;
                            }else{
                                return 1;
                            }
                        })
                    })

                    this.top_msg();
                })
            },

            /*格式化时间*/
            time_format(time) {
                let time_arr = time.substr(2).replace("T", " ").replace(/-/g, "/").split(":");
                time_arr.pop();
                return time_arr.join(":");
            },

            /*开启视频层*/
            open_video_matte(title, video){
                this.matte_video = {
                    state: "show",
                    title: title,
                    video: video
                };
            },

            /*上传图片*/
            upload_img(e){
                let form_data = new FormData();
                form_data.append("file", e.currentTarget.files[0]);
                this.$axios({
                    method: 'post',
                    url: "http://wyrun.com/btx/index/upload_img/",
                    data: form_data,
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }).then(res => {
                    if(this.$utils.cheack_device(this.$route) === "pc"){//pc端可编辑富文本发送
                        this.tinymce.html += "<p><img src='" + res.data.location + "' /></p><p></p>";
                    }else{//手机端直接发送
                        this.tinymce.html = "<p><img src='" + res.data.location + "' /></p>";
                        this.send_msg();
                    }
                })
            },

            //【手机端】--------------------------------------------------------------------------

            /*返回列表*/
            back_list(){
                this.$refs.chatRecord.setAttribute("state", "");
                this.$refs.tinymce.setAttribute("state", "");
            },

        },
        watch: {

            /*通讯录列表*/
            mailList(classmate){
                this.mailList = this.mail_list = classmate;
                this.init_msg();
            }

        },
        beforeMount(){

            /*上传图片组件*/
            let _this = this;
            this.tinymce.setting.toolbar = 'img emoticons';
            this.tinymce.setting.setup = function(editor) {
                editor.addButton('img', {
                    icon: "image",
                    onclick: function(){
                        if(_this.chat_record.talk_to.uid){
                            _this.$refs["img_uploader"].click();
                        }else{
                            _this.confirm.toast("请先选择通讯录联系人", 2000, "notic");
                        }
                    }
                });
            };

        },
        mounted(){
            this.confirm = this.$B.widgets.Confirm;

            /*定时刷新*/
            setInterval(() => {
                this.init_msg();
            }, 60000)

            /*终端交互*/
            if(this.$utils.cheack_device(this.$route) === "pc"){//pc端设置聊天区域自适应最大高度
                this.$refs.chatRecord.style["max-height"] = window.innerHeight - 520 + "px";//动态设置聊天区域高度
            }else{//移动端移除上传按钮的touchstart事件
                let t = setInterval(()=>{
                    var mceu0 = document.querySelector(".mce-first");
                    if(mceu0){
                        mceu0.addEventListener("touchstart", function(e){
                            e.stopPropagation();
                        });
                        clearInterval(t);
                    }
                }, 1000)
            }

        },
        components: {
            "head-widget": Head,
            "tiny-editor": Editor,
            "matte-video-widget": MatteVideo
        }
    }
</script>

<style scoped>
    @import "../../assets/css/head-widget.css";
    @import "../../assets/css/search-widget.css";
    @import "../../assets/css/mail-list-widget.css";
    @import "../../assets/css/no-result-widget.css";
    @import "../../assets/css/base-btn-widget.css";
    @import "../../assets/css/tinymce-widget.css";
</style>
<style>
    @import "../../assets/css/chat-record-widget.css";
</style>


