<template>
    <div style="padding-bottom: 3rem">
        <h2 class="main-title-widget">
            <router-link class="mt-w-link" :to="back_start">路线图</router-link>
            <span class="mt-w-text">{{cur_class.name}}</span>
        </h2>
        <div class="learn-outline-widget" v-if="cur_class.sub_class.length > 0">
            <dl class="lo-w-list pannel-widget" v-for="(chapter, index) of cur_class.sub_class">
                <dt class="base-title-widget"><h3>第 {{index+1}} 章: {{chapter.name}}</h3></dt>
                <dd class="grid lo-w-list-items">
                    <div class="col-6" :id="chapter.sub_c_id" v-for="(task, t_index) of chapter.tasks">
                        <router-link :to="'/mob/learn_detail?sub_c_id=' + chapter.sub_c_id + '&task_id=' + task.id" class="lo-w-list-item lo-w-list-item-btn">
                            <div class="lo-w-list-item-cover">任务 {{t_index+1}}</div>
                            <div class="lo-w-list-item-name">{{task.name}}</div>
                        </router-link>
                    </div>
                </dd>
            </dl>
        </div>
        <div class="pannel-widget" v-else>
            <div class="no-result-widget">
                <span class="icon-nodata nr-w-ico"></span>
                <span class="nr-w-text">暂无课程章节</span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "learn_path",
        data() {
            return {

                /*当前课程*/
                cur_class: {
                    name: "",
                    sub_class: []
                },

                /*上级路线图*/
                back_start: "/mob/learn_start?class_id=" + this.$store.state.major

            }
        },
        mounted() {
            this.$axios.all([
                this.$axios.get("chapter/"),
                this.$axios.get("complete/")
            ]).then(this.$axios.spread((r1, r2) => {
                let sub_class = [];
                r1.data.forEach(data => {
                    if (data.course_name.id == this.$route.query.cid) {
                        let complete = false;

                        /*该章节是否完成*/
                        r2.data.forEach( comp => {
                            if(comp.chapter.id == data.id && comp.chapter_end === true){
                                complete = true;
                                return;
                            }
                        })

                        sub_class.push({
                            sub_c_id: data.id,
                            name: data.chapter_name,
                            desc: data.chapter_introduce,
                            complete: complete,
                            tasks: data.tasks
                        });
                    }
                })

                /*执行初始化*/
                this.cur_class = {
                    name: this.$route.query.cname,
                    sub_class: sub_class
                };

                this.$nextTick(function(){
                    document.documentElement.scrollTop = document.body.scrollTop = 60;
                })
            }))
        }
    }
</script>

<style scoped>
    @import "../../assets/css/main-title-widget.css";
    @import "../../assets/css/class-path-widget.css";
    @import "../../assets/css/no-result-widget.css";
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/learn-outline-widget.css";
    @import "../../assets/css/base-title-widget.css";
</style>


