<template>
    <div class="nav-widget">
        <div class="container n-w-box grid">
            <div class="n-w-left">
                <router-link :to="path_prefix + '/'"><img class="n-w-logo" :src="logo_src.image"/></router-link>
            </div>
            <ul class="n-w-list">
                <li class="n-w-item" v-for="nav of navs">
                    <router-link class="n-w-text" :to="nav.to" :state="nav.state">{{nav.name}}</router-link>
                </li>
            </ul>
            <div class="n-w-right">
                <span v-if="user.uid==''">
                    <router-link :to="path_prefix + '/user_home'" state="login" class="head-widget">
                        <span class="icon-login h-w-login-ico"></span>
                    </router-link>
                    <router-link :to="path_prefix + '/user_home'" >登录</router-link>
                </span>
                <span v-else>
                    <router-link :to="path_prefix + '/user_home'">
                        <head-widget :uname="user.uname"></head-widget>
                    </router-link>
                    <a @click="exit_login">退出</a>
                </span>
            </div>
        </div>
        <div class="n-w-mob-box">
            <span v-for="nav of navs">
                <router-link class="n-w-mob-text" :to="path_prefix + nav.to" :state="nav.state">{{nav.name}}</router-link>
            </span>
        </div>
    </div>
</template>

<script>
    import Head from "@/components/widget/head-widget"

    export default {
        name: 'nav-widget',
        data() {
            return {

                /*基于终端的路径前缀*/
                path_prefix: "",

                /*主导航*/
                navs: [
                    {
                        name: "路线图",
                        to: "/learn_start",
                        state: "",
                    },
                    {
                        name: "图书馆",
                        to: "/library",
                        state: "",
                    },
                    {
                        name: "竞技场",
                        to: "/arena_ranking_list",
                        state: "",
                    },
                    {
                        name: "咖啡厅",
                        to: "/coffee/teacher",
                        state: "",
                    },
                ],

                /*logo*/
                logo_src: "",

            }
        },
        methods: {

            /*退出登录*/
            exit_login(){
                this.$store.commit("exit_login");
                this.$router.push(this.path_prefix + "/");
            },

            /*当前导航选中状态*/
            nav_act(path){
                this.navs.forEach(function (nav) {
                    if (nav.state === "act") nav.state = "";
                    if (nav.to.split("/").pop().split("?")[0] === path.split("/").pop().split("?")[0]) {
                        nav.state = "act";
                    }
                })
            }

        },
        computed: {

            /*用户信息*/
            user() {
                return this.$store.state.user;
            },

            /*一级课程*/
            major(){
                return this.$store.state.major;
            }

        },
        beforeMount(){
            this.path_prefix = this.$utils.cheack_device(this.$route) === "mob"? "/mob": "";
            this.$utils.get_res("logo_src", this);
            this.nav_act(this.$router.history.current.fullPath);
            this.navs[0].to = "/learn_start/?class_id=" + this.major;
        },
        watch: {
            $route(to) {
                this.nav_act(to.path);
            },
            major(m){
                this.navs[0].to = "/learn_start/?class_id=" + m;
            }
        },
        components: {
            "head-widget": Head,
        }
    }
</script>

<style scoped>
    @import "../../assets/css/nav-widget.css";
    @import "../../assets/css/head-widget.css";
</style>


