<template>
    <div>
        <tab-widget :tabs="tabs"></tab-widget>
        <editor-widget :mail-list="teachers" obj="teacher"></editor-widget>
        <div class="pad-h-2" ></div>
    </div>
</template>

<script>
    import Tab from "@/components/widget/tab-widget";
    import Editor from "@/components/widget/editor-widget";

    export default {
        name: "teacher",
        data() {
            return {

                /*tab-widget*/
                tabs: [
                    {
                        name: "老师",
                        state: "act",
                        type: "route",
                        link: "/coffee/teacher",
                    },
                    {
                        name: "同班",
                        type: "route",
                        unread: "",
                        link: "/coffee/same_class",
                    },
                    {
                        name: "小组",
                        type: "route",
                        unread: "",
                        link: "/coffee/team",
                    },
                    {
                        name: "同学",
                        type: "route",
                        link: "/coffee/student",
                    },
                ],

                /*通讯老师*/
                teachers: [],

            }
        },
        components: {
            "tab-widget": Tab,
            "editor-widget": Editor,
        },
        mounted() {
            this.$axios.get("user_class/")
                .then(r1 => {//初始化老师列表
                    let teachers = r1.data;
                    teachers.forEach(teacher => {
                        let th = teacher.teacher_head,       //导师
                            tl = teacher.teacher_lector,     //班主任
                            tt = teacher.teacher_tutor;      //讲师
                        this.teachers = [
                            {
                                uid: th.id,
                                state: "",
                                name: th.name,
                                suffix: th.intr,
                                unread: 0
                            },
                            {
                                uid: tl.id,
                                state: "",
                                name: tl.name,
                                suffix: tl.intr,
                                unread: 0
                            },
                            {
                                uid: tt.id,
                                state: "",
                                name: tt.name,
                                suffix: tt.intr,
                                unread: 0
                            },
                        ]
                    })
                })
        },
    }
</script>

<style scoped>
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/basic-article-widget.css";
    @import "../../assets/css/base-btn-widget.css";
    @import "../../assets/css/head-widget.css";
</style>



