<template>
    <div>
        <tab-widget :tabs="tabs"></tab-widget>
        <div class="base-table-widget">
            <table class="bt-w-table">
                <thead>
                <tr class="bt-w-title">
                    <td class="bt-w-title-td">主题</td>
                    <td class="bt-w-title-td pcenter">用户</td>
                    <td class="bt-w-title-td pcenter">回复</td>
                </tr>
                </thead>
                <tbody>
                <tr @click="check_msg" :id="msg.article_id" class="bt-w-tr" v-for="msg of msges">
                    <td class="bt-w-td">{{msg.title}}</td>
                    <td class="bt-w-td pcenter">
                        <head-widget :uname="msg.uname"></head-widget>
                    </td>
                    <td class="bt-w-td pcenter">{{msg.reply}}</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
    import Tab from "@/components/widget/tab-widget";
    import Head from "@/components/widget/head-widget";

    export default {
        name: "student",
        data() {
            return {

                /*tab-widget*/
                tabs: [
                    {
                        name: "老师",
                        type: "route",
                        link: "/mob/coffee/teacher",
                    },
                    {
                        name: "同班",
                        type: "route",
                        link: "/mob/coffee/same_class",
                    },
                    {
                        name: "小组",
                        type: "route",
                        link: "/mob/coffee/team",
                    },
                    {
                        name: "同学",
                        state: "act",
                        type: "route",
                        link: "/mob/coffee/student",
                    },
                ],

                /*最新论坛消息*/
                msges: [],

            }
        },
        methods: {

            /*查看论坛消息*/
            check_msg(e){
                this.$router.push("/mob/blog?article_id=" + e.currentTarget.id);
            }

        },
        components: {
            "tab-widget": Tab,
            "head-widget": Head,
        },
        mounted(){
            this.$axios.get("discuss_msg/")
                .then( res => {
                    let msges = res.data;
                    this.msges = this.$B.data_convert(msges, {
                        article_id: "id",
                        title: "title",
                        uname: (data)=>{
                            return data.user.name;
                        },
                        reply: (data)=>{
                            return data.discuss_replay.length;
                        },
                        browse: "click_num",
                        active: (data)=>{
                            return data.send_time.replace("T", " ");
                        },
                    })
                    this.msges.reverse();
                })
        }
    }
</script>

<style scoped>
    @import "../../../assets/css/head-widget.css";
    @import "../../../assets/css/chat-record-widget.css";
    @import "../../../assets/css/base-table-widget.css";
    @import "../../../assets/css/base-btn-widget.css";
</style>


